<template>
<div>
  <div class="goods">
    <div class="menu-wrapper" ref="mw">
      <ul v-if="this.goods.length">
        <li
          class="menu-item"
          v-for="(item, index) in goods"
          :key="index"
          :class="{ current: currentIndex === index }"
          @click="selectMenu(index)"
        >
          <support-ico :type="item.type" :size="2" v-if="item.type !== -1" />
          {{ item.name }}
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="fw">
      <ul>
        <li class="food-list" v-for="(item, index) in goods" :key="index" ref="foodList">
          <h1 class="title">{{ item.name }}</h1>
          <ul>
            <li class="food-item" v-for="(food, idx) in item.foods" :key="idx">
              <div class="icon">
                <img :src="food.icon" />
              </div>
              <div class="content">
                <h2 class="name">{{ food.name }}</h2>
                <p class="desc">{{ food.description }}</p>
                <div class="extra">
                  <span class="count">月售{{ food.sellCount }}</span>
                  <span>好评{{ food.rating }}%</span>
                </div>
                <div class="price">
                  <span class="now">￥{{ food.price }}</span>
                  <span class="old" v-if="food.oldPrice"
                    >￥{{ food.oldPrice }}</span>
                </div>

                <div class="cartcontrol-wrapper">
                  <cart-control :food="food"></cart-control>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
  </div>

  <shop-cart :foods="selectFoods" :deliveryPrice="seller.deliveryPrice" :minPrice="seller.minPrice" v-if="seller"></shop-cart>
</div>
</template>

<script>
import { getGoods } from "@/api"
import SupportIco from "@/components/support-ico/support-ico.vue"
import CartControl from "@/components/cart-control/cart-control.vue"
import ShopCart from "@/components/shop-cart/shop-cart.vue"

import BScroll from "better-scroll"

export default {
  props: {
    seller: {
      type: Object
    }
  },
  components: {
    SupportIco,
    CartControl,
    ShopCart
  },
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      food: {},
      foods: []
    }
  },
  created() {
    getGoods().then((res) => {
      this.goods = res
      console.log(res)
      this.$nextTick(() => {
        // 确保一定能拿到 ref 
        this._initBS()

        this._calculateHeight()
      })
    })
  },
  methods: {
    selectMenu(index) {
      let foodList = this.$refs.foodList
      let el = foodList[index]
      this.fws.scrollToElement(el, 300)
    },
    _initBS() {
      this.fws = new BScroll(this.$refs.fw, {
        click: true,
        probeType: 3
      })
      this.mws = new BScroll(this.$refs.mw, {
        click: true,
        probeType: 3
      })
      this.fws.on('scroll', pos => {
        this.scrollY = Math.abs(pos.y)
      })
    },
    _calculateHeight() {
      let foodList = this.$refs.foodList
      let height = 0
      this.listHeight.push(height)
      foodList.forEach((item) => {
        height += item.clientHeight
        this.listHeight.push(height)
      })
    }
  },
  computed: {
    currentIndex() {
      let res = 0
      this.listHeight.forEach((item,index) => {
        if(this.scrollY >= item && this.scrollY < this.listHeight[index+1]) res = index
      })
      return res
    },
    selectFoods() {
      let foods = []
      for (let good of this.goods) {
        for (let food of good.foods) {
          if(food.count) {
            foods.push(food)
          }
        }
      }
      return foods
    }
  }
}
</script>

<style lang="less">
.goods {
  display: flex;
  position: absolute;
  top: 177px;
  bottom: 46px;
  overflow: hidden;
  .menu-wrapper {
    flex: 0 0 80px;
    width: 80px;
    background: #f3f5f7;
    .menu-item {
      display: flex;
      width: 60px;
      height: 54px;
      padding: 0 10px;
      text-align: center;
      justify-content: center;
      align-items: center;
      line-height: 14px;
      font-size: 10px;
    }
    .menu-item.current {
      background: #fff;
      font-weight: 700;
    }
  }
  .foods-wrapper {
    flex: 1;
    .title {
      padding-left: 14px;
      height: 26px;
      line-height: 26px;
      border-left: 2px solid #d9dde1;
      font-size: 12px;
      color: rgb(147, 153, 159);
      background: #f3f5f7;
    }
    .food-item {
      display: flex;
      margin: 18px;
      padding-bottom: 18px;
      position: relative;
      .icon {
        flex: 0 0 57px;
        margin-right: 10px;
        img {
          width: 100%;
        }
      }
      .content {
        flex: 1;
        .name {
          margin: 2px 0 8px 0;
          height: 14px;
          line-height: 14px;
          font-size: 14px;
          color: rgb(7, 17, 27);
        }
        .desc,
        .extra {
          line-height: 10px;
          font-size: 10px;
          color: rgb(147, 153, 159);
        }
        .desc {
          line-height: 12px;
          margin-bottom: 8px;
        }
        .extra {
          .count {
            margin-right: 12px;
          }
        }
        .price {
          font-weight: 700;
          line-height: 24px;
          .now {
            margin-right: 8px;
            font-size: 14px;
            color: rgb(240, 20, 20);
          }
          .old {
            text-decoration: line-through;
            font-size: 10px;
            color: rgb(147, 153, 159);
          }
        }
        .cartcontrol-wrapper{
          position: absolute;
          right: 0;
          bottom: 12px;
        }
      }
    }
    .food-item:last-child {
      margin-bottom: 0;
    }
  }
}
</style>