<template>
  <span class="support-ico icon-1 decrease"  :class='iconCls'></span> 
</template>

<script>
export default {
  props: {
    size: {
      type: Number,
    },
    type: {
      type: Number,
    }
  },
  computed: {
    iconCls() {
      const classMap = ['decrease', 'discount', 'special', 'invoice', 'guarantee']
      return classMap[this.type]
    }
  }
}
</script>

<style lang="less">
.support-ico {
  display: inline-block;
  background-repeat: no-repeat;
}
.icon-1 {
  width: 12px;
  height: 12px;
  background-size: 12px 12px;
}

.icon-2 {
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
}
.decrease {
  background-image: url("./support-ico-image/decrease_1@2x.png");
}
.discount{
  background-image: url("./support-ico-image/discount_1@2x.png");
}
.special{
  background-image: url("./support-ico-image/special_1@2x.png");
}
.invoice{
  background-image: url("./support-ico-image/invoice_1@2x.png");
}
.guarantee{
  background-image: url("./support-ico-image/guarantee_1@2x.png");
}
</style>